body {
  color: #333;
  background: #f0f4f8;
  margin: 0;
  padding: 20px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

h1 {
  text-align: center;
  color: #2c3e50;
  margin-bottom: 20px;
}

#search-box {
  border: 2px solid #3498db;
  border-radius: 6px;
  width: 100%;
  max-width: 400px;
  margin: 0 auto 30px;
  padding: 10px 15px;
  font-size: 18px;
  transition: border-color .3s;
  display: block;
}

#search-box:focus {
  border-color: #2980b9;
  outline: none;
  box-shadow: 0 0 8px #2980b980;
}

.country-list {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 6px;
  max-width: 400px;
  max-height: 250px;
  margin: 0 auto 20px;
  padding: 0;
  list-style: none;
  overflow-y: auto;
  box-shadow: 0 2px 5px #0000001a;
}

.country-list li {
  cursor: pointer;
  border-bottom: 1px solid #eee;
  padding: 10px 15px;
  transition: background-color .2s;
}

.country-list li:hover {
  color: #fff;
  background-color: #3498db;
}

.country-info {
  color: #2c3e50;
  background: #fff;
  border-radius: 6px;
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 8px #00000026;
}

.country-info h2 {
  margin-top: 0;
  margin-bottom: 15px;
}

.country-info p {
  margin: 6px 0;
  font-size: 16px;
  line-height: 1.4;
}

.country-info img {
  border: 1px solid #ddd;
  border-radius: 4px;
  max-width: 100%;
  height: auto;
  margin-top: 15px;
  display: block;
}

.country-list::-webkit-scrollbar {
  width: 8px;
}

.country-list::-webkit-scrollbar-thumb {
  background-color: #3498db;
  border-radius: 4px;
}

.country-list::-webkit-scrollbar-track {
  background: #f0f4f8;
}
